import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from 'react-router-dom';
import styled from "styled-components";
import "./Detail.scss";

let Box = styled.div`
  padding : 20px;
`;

let 제목 = styled.h1`
  font-size : 25px;
  color : ${props => props.col}
`;

function Detail(props) {

  let { id } = useParams();
  let findGoods = props.shoes.find(x => x.id == id);
  let [count, count_edit] = useState(true);
  let [write, write_edit] = useState('');

  useEffect(() => {
    let timer = setTimeout(() => { count_edit(false) }, 2000);
    return () => { clearTimeout(timer) }
  }, []);

  let history = useHistory();
  return (
    <div className="container">
      <Box>
        <제목 className="red">Detail</제목>
      </Box>

      <input onChange={(e) => { write_edit(e.target.value) }} />

      {
        count === true
          ? (<div className="my-alert2">
            <p>재고가 별로 안남음</p>
          </div>)
          : null
      }
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%"></img>
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{findGoods.title}</h4>
          <p>{findGoods.content}</p>
          <p>{findGoods.price}</p>
          <Info inventory={props.inventory} />
          <button className="btn btn-danger" onClick={() => {
            props.inventory_edit([9, 11, 12])
          }}>주문하기</button>
          <button className="btn btn-danger" onClick={() => {
            history.push('/')
          }}>뒤로가기</button>
        </div>
      </div>
    </div >
  );
}

function Info(props) {
  return (
    <p>남은 재고 : {props.inventory[0]} </p>
  );
}

export default Detail;